import React, { useState, useRef, useEffect } from 'react'
import { Container } from '../globalStyle'
import facebookLogo from '../img/icons/facebook.svg'
import instaLogo from '../img/icons/instagram.svg'
import increate from '../img/icons/increate-copyright.svg'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import emailjs from 'emailjs-com'

const FormSection = () => {
    const sectionRef = useRef()

    const [userData, setUserData] = useState({ 
        name: '', 
        phoneNumber: '',  
        message: ''
    })

    const sendForm = (e) => {
        e.preventDefault()

        emailjs.sendForm('service_32kwssc', 'template_yurhta7', e.target, 'user_eO2gN2GSK1ynAfE7E7CnS')
          .then((result) => {
              alert(result.text);
          }, (error) => {
              alert(error.text);
          });


        setUserData({
            name: '', 
            phoneNumber: '',  
            message: ''
        })
    }

    useEffect(() => {
        ScrollTrigger.create({
            trigger: sectionRef.current,
            pin: true,
            start: 'top top'
        })
    }, [])

    return(
        <section ref={sectionRef} className="form-section section">
            <Container className="_container">
                <div className="form-section__wrapper">
                    <div className="form-section__content">
                        <div className="contacts-wrapper">
                            <div>
                                <p className="_text-on-scroll">Контакти</p>
                                <ul className="contacts-links">
                                    <li>
                                        <a className="_text-on-scroll" href="tel:+380506789924">+38 050 678 99 24</a>
                                    </li>
                                    <li>
                                        <a className="_text-on-scroll" href="mailto:swim_system@examle.com">swim_system@examle.com</a>
                                    </li>
                                </ul>
                            </div>
                            <ul className="contacts-social">
                                <li>
                                    <a href="http:facebook.com" target="_blank" rel="noopener noreferrer">
                                        <img src={facebookLogo} alt="Swim System" />
                                    </a>
                                </li>
                                <li>
                                    <a href="http:insta.com" target="_blank" rel="noopener noreferrer">
                                        <img src={instaLogo} alt="Swim System" />
                                    </a>
                                </li>
                            </ul>
                        </div>
                        <div className="form-wrapper">
                            <form onSubmit={sendForm} className="form" action="#">
                                <div className="form__input-group">
                                    <input 
                                        name="name"
                                        autoComplete="off"
                                        className="form-input"
                                        value={userData.name} 
                                        placeholder="Ім’я"
                                        onChange={(e) => setUserData({
                                            ...userData,
                                            name: e.target.value
                                        })} 
                                    />
                                </div>
                                
                                <div className="form__input-group">
                                    <input
                                        name="phone"
                                        autoComplete="off"
                                        className="form-input"
                                        value={userData.phoneNumber}
                                        placeholder="Телефон"
                                        onChange={(e) => setUserData({
                                            ...userData,
                                            phoneNumber: e.target.value
                                        })} 
                                    />
                                </div>

                                <div className="form__input-group">
                                    <input
                                        name="message"
                                        autoComplete="off"
                                        className="form-input"
                                        value={userData.message}
                                        placeholder="Повідомлення"
                                        onChange={(e) => setUserData({
                                            ...userData,
                                            message: e.target.value
                                        })} 
                                    />
                                </div>

                                <button 
                                    className="form-button"
                                    type="submit"
                                >
                                    <p>Відправити</p>
                                </button>
                            </form>
                        </div>
                    </div>
                    <div className="footer">
                        <div className="side-1 _text-on-scroll">
                            &copy; 2021 Swim System
                        </div>
                        <div className="side-2">
                            <a className="_element-on-scroll" href="http://" target="_blank" rel="noopener noreferrer">
                                <img src={increate} alt="Increate" />
                            </a>
                        </div>
                    </div>
                </div>
            </Container>
        </section>
    )
}

export default FormSection